import React, { useEffect, useRef } from 'react'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'

import ArrowBackIcon from '@mui/icons-material/ArrowBack'
import { Box, Button, Card, CardHeader, Checkbox, Container, Divider, Typography } from '@mui/material'
import type { Editor } from '@toast-ui/react-editor'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'
import TextEditor from 'src/components/TextEditor'

import { useGetConferenceRound, usePatchConferenceRound } from 'src/hooks/api/clubConferenceHooks'

const MeetingNotes = () => {
  const navigate = useNavigate()
  const handleClickNavigateBack: React.MouseEventHandler<HTMLButtonElement> = () => {
    navigate(-1)
  }
  const editorRef = useRef<Editor>(null)
  const { id, confId, meetingId } = useParams()
  const clubId = Number(id)
  const conferenceId = Number(confId)
  const conferenceRoundId = Number(meetingId)
  const { data, isSuccess } = useGetConferenceRound(clubId, conferenceId, conferenceRoundId)
  const [isPublic, setIsPublic] = React.useState<boolean>(false)

  useEffect(() => {
    if (isSuccess && data) {
      setIsPublic(data.isPublic)
    }
  }, [isSuccess, data])

  const patchRoundMutation = usePatchConferenceRound()
  const handleSubmit = () => {
    const content = editorRef.current?.getInstance().getHTML() || ''
    isSuccess
      ? patchRoundMutation.mutate({
          clubId,
          conferenceId,
          conferenceRoundId,
          note: content,
          startAt: data?.startAt,
          isPublic
        })
      : null
    navigate(-1)
  }
  return (
    <Box m={3}>
      <Helmet>
        <title>활동 내용 작성</title>
      </Helmet>
      <Container>
        <Card>
          <Box
            sx={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center', p: 1 }}
          >
            <Box sx={{ display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
              <Button variant="text" onClick={handleClickNavigateBack} color="primary">
                <ArrowBackIcon />
              </Button>
              <CardHeader title={`${data?.round} 회차 활동 내용`} />
            </Box>
            <Box>
              <Typography display="inline-block" variant="body1">
                전체 공개
              </Typography>
              <Checkbox sx={{ padding: '5px' }} checked={isPublic} onChange={e => setIsPublic(e.target.checked)} />
            </Box>
          </Box>
          <Divider />
          {isSuccess && (
            <TextEditor
              initialValue={data.note}
              editorRef={editorRef}
              toolbarItems={[
                ['heading', 'bold', 'italic', 'strike'],
                ['hr', 'quote'],
                ['ul', 'ol', 'task'],
                ['table', 'image', 'link'],
                ['code', 'codeblock']
              ]}
            />
          )}
        </Card>
        <CancelSubmitBtns handleSubmit={handleSubmit} submitBtnText="등록 / 수정" />
      </Container>
    </Box>
  )
}

export default MeetingNotes
